.header{
    width: 100%;
    height: vw(111);
    display: flex;
    align-items: center;
    z-index: 5;
    background: linear-gradient(180deg, rgba(40, 40, 40, 0.28) 0%, rgba(40, 40, 40, 0.1036) 100%);
    backdrop-filter: blur(14px);

    .header__container{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.header__logo{
    width: vw(227);
    height: vw(64);

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.header__menu{
    .header__burger{
        width: vw(46);
        height: vw(32);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin-right: vw(40);
        cursor: pointer;

        span{
            width: 100%;
            height: vw(4);
            background-color: #fff;
            border-radius: vw(5);
        }
    }
}

.header__contacts{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-left: vw(125);

    a.header__number{
        font-family: $DTW-Medium;
        color: #fff;
        font-size: vw(20);
    }

    p.header__schedule{
        color: rgba($color: #fff, $alpha: 0.8);
        font-size: vw(14);
        font-family: $MS-Light;
        padding-top: vw(10);
    }
}

.header__where{
    display: flex;
    align-items: center;

    .header__map{
        width: vw(91);
        height: vw(47);
        filter: drop-shadow(0px 0px vw(12) #00FFFF);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    p{
        padding-left: vw(10);
        font-size: vw(14);
        font-family: $MS-Regular;
        color: rgba($color: #fff, $alpha: 0.8);
        line-height: vw(20);
    }
}

@media (max-width: 990px){
    .header{
        height: vmin(121);
    
        .header__container{

            .btn{
                display: none;
            }
        }
    }

    .header__where{
        display: none;
    }

    .header__logo{
        width: vmin(108);
        height: vmin(31);

    }

    .header__menu{
        .header__burger{
            width: vmin(25);
            height: vmin(21);
            margin-right: 0;
            cursor: pointer;
    
            span{
                width: 100%;
                height: vmin(1.8);
                background-color: #fff;
                border-radius: vmin(3);
            }
        }
    }

    .header__contacts{
        margin-left: 0;
        a.header__number{
            font-size: vmin(15);
        }
    
        p.header__schedule{
            font-size: vmin(11);
            padding-top: vmin(5);
        }
    }
}